// Inquiry Actions
export async function updateInquiryStatusAction(id: string, status: ClientInquiryStatus) {
  console.log(`Updating inquiry ${id} to status ${status} (simulated in-memory)`);
  const inquiryIndex = clientInquiries.findIndex(inq => inq.id === id);
  if (inquiryIndex !== -1) {
    clientInquiries[inquiryIndex].status = status;
  } else {
    console.warn(`updateInquiryStatusAction: Inquiry with ID ${id} not found.`);
  }
  revalidatePath("/admin/inquiries");
  revalidatePath("/admin"); // Dashboard
}

export async function submitQuoteRequestAction(data: QuoteRequestFormValues): Promise<{ success: boolean; message: string }> {
  console.log("Server action: submitQuoteRequestAction called with data:", data);
  try {
    const newInquiry: ClientInquiry = {
      id: `inq-${Date.now()}-${Math.random().toString(36).substring(2, 7)}`,
      name: data.name,
      email: data.email,
      company: data.company,
      serviceRequested: data.service,
      details: data.projectDetails,
      date: new Date().toISOString(),
      status: 'New',
    };
    clientInquiries.unshift(newInquiry); // Add to the beginning of the array for recent first
    
    console.log("New inquiry added to clientInquiries (simulated):", newInquiry);
    console.log("Current clientInquiries:", clientInquiries.length);


    revalidatePath("/admin/inquiries");
    revalidatePath("/admin"); // For dashboard stats
    
    return { success: true, message: "Your quote request has been submitted successfully and logged!" };
  } catch (error) {
    console.error("Error in submitQuoteRequestAction:", error);
    return { success: false, message: "There was an error submitting your request on the server." };
  }
}
